
.input-search{
    
    margin-right: 30px;
    margin-bottom: 10px;
    padding: 1rem;
    float: left;
    border: none;
    width: 40%;
    border-radius: 10rem;
    transition: all 0.2s ease-in;
}

.input-search:focus {
    outline: none;
}
.btn {
    padding: .8rem 4rem;
    background-color: #e74c3c;
    color: #ecf0f1;
    border: none;
    border-radius: 10rem;
    float: left;
    outline: none;
    transition: all 0.1s ease-in;
}
.btn:active{
    transform: translateY(2px);
}

@media only screen and (max-width: 820px) {

    .input-search {
        width: 80%;
        margin:20px;
    }
    .btn{
        margin-left: 20px;
    }
    
}